import React, { forwardRef, useEffect, useImperativeHandle, useRef } from "react";
import Editor, { EditorProps } from "@monaco-editor/react";
// @ts-ignore: Unreachable code error
import ConstrainedEditor from "constrained-editor-plugin";
import { DEFAULT_CODE } from "./data";
interface IEditorProps extends EditorProps {
  test?: string;
  disabled?: boolean;
}
export interface IRuleEditorRef {
  getValue: () => string;
}
const DELIMITER_TEXT_FOR_RULE = "// Write Rule Here";
const MonacoEditor = forwardRef<IRuleEditorRef, IEditorProps>((props: IEditorProps, ref) => {
  const { value, disabled, ...rest } = props;
  const [rule, setRule] = React.useState(value);
  const [defaultValue, setDefaultValue] = React.useState(DEFAULT_CODE);
  const editBelowLineText = "// WRITE RULE BELOW THIS LINE ---------- VVVVV ----------";
  const editAboveLineText = "// WRITE RULE ABOVE THIS LINE ---------- ^^^^^^ ----------";

  const editorRef = useRef<any>(null);

  useEffect(() => {
    if (value) {
      setDefaultValue(
        DEFAULT_CODE.split(DELIMITER_TEXT_FOR_RULE).join(`${DELIMITER_TEXT_FOR_RULE} \n` + value)
      );
    }
  }, []);

  useEffect(() => {
    if (editorRef.current) {
      editorRef.current.updateOptions({ readOnly: disabled });
    }
  }, [disabled]);

  function handleEditorDidMount(editor: any, monaco: any) {
    editorRef.current = editor;
    const model = editor.getModel();
    const constrainedInstance = ConstrainedEditor(monaco);
    constrainedInstance.initializeIn(editor);

    const initialEditableBelowLineNo = findLineNumber(editor, editBelowLineText) + 1;
    const initialEditableAboveLineNo = findLineNumber(editor, editAboveLineText);

    //these restrictions will make only a part of the code editable.
    constrainedInstance.addRestrictionsTo(model, [
      {
        range: [initialEditableBelowLineNo, 19, initialEditableAboveLineNo, 1], // Range of Function definition
        allowMultiline: true,
        label: "funcDefinition",
      },
    ]);
    editor.updateOptions({ readOnly: disabled });

    // Next Line will hide some areas in code:
    // editor.setHiddenAreas([{ startLineNumber: 1, endLineNumber: 5 }]);
    editor.onDidChangeModelContent(() => {
      const modifiedLines = [];
      const startLineNumber = initialEditableBelowLineNo;
      const endLineNumber = editor.getModel().getLineCount();

      for (let i = startLineNumber; i <= endLineNumber; i++) {
        modifiedLines.push(editor.getModel().getLineContent(i));
      }
      modifiedLines.splice(modifiedLines.length - 3, 3);
      //ModifiedString contains the code which is modified by the user. This is stored in the state => rule.
      const modifiedString = modifiedLines.join("\n");
      setRule(modifiedString);
    });
  }

  function findLineNumber(editor: any, searchText: any) {
    const model = editor.getModel();
    const lineCount = model.getLineCount();

    for (let i = 1; i <= lineCount; i++) {
      const lineContent = model.getLineContent(i);
      if (lineContent.includes(searchText)) {
        return i; // Return the line number
      }
    }

    return -1; // Return -1 if not found
  }

  useImperativeHandle(ref, () => ({
    getValue: () => {
      return rule?.replace(DELIMITER_TEXT_FOR_RULE, "").trim() ?? "";
    },
  }));

  return (
    <div className="App">
      <Editor
        value={defaultValue}
        height="60vh"
        language="typescript"
        // theme="vs-dark"
        options={{ cursorBlinking: "smooth" }}
        onMount={handleEditorDidMount}
      />
    </div>
  );
});

export default MonacoEditor;
