/*
  -------------------------------------------------------
   Imports:
  -------------------------------------------------------
*/
import AclGuard from "components/AclGuard/AclGuard";
import ButtonBox from "components/ButtonBox";
import ErrorDialog from "components/ErrorDialog/ErrorDialog";
import InputBox from "components/InputBox";
import InputTextArea from "components/InputTextArea";
import Label from "components/Label/Label";
import Loader from "components/Loader/Loader";
import MonacoEditor, { IRuleEditorRef } from "components/MonacoEditor/MonacoEditor";
import PageTitle from "components/PageTitle/PageTitle";
import ToastCustom from "components/Toast";
import { RouteProps, withRouter } from "hoc/withRouter";
import { ToastPositionType } from "primereact/toast";
import queryString from "query-string";
import { useEffect, useRef, useState } from "react";
import { linkProductConfigurationBusinessRule } from "routes";
import BusinessRuleService from "services/masterdata/ProductConfig/BusinessRuleService";
import SimpleReactValidator from "simple-react-validator";
import {
  getStandardValidator,
  messageReplace,
  parseStandardAPIErrorMessage,
  stringifyObject,
  validateGetAPIResponse,
  validatePostAPIResponse,
} from "utils/CommonUtil";
import {
  ACTIVE_TYPE,
  BUTTON_TYPE,
  ENTITY,
  INPUT_STYLE_TYPE,
  OPERATION_TYPE,
  PAGE_MODE,
  STATIC_MASTER_TYPE_TOAST_TEXT,
  TOAST_STYLE_TYPE,
} from "utils/Constant";
/*
  -------------------------------------------------------
  PROPS:
  -------------------------------------------------------
*/

const PageBusinessRulesCM = (props: RouteProps) => {
  /*
    -------------------------------------------------------
      States
    -------------------------------------------------------
  */

  const [pageObj, setPageObj] = useState({
    ruleName: "",
    rule: "",
    description: "",
    reasons: "",
    isActive: ACTIVE_TYPE.ACTIVE,
  });
  const [pageMode, setPageMode] = useState(PAGE_MODE.CREATE);
  const [initialObj, setInitialObj] = useState({
    ruleName: "",
    rule: "",
    description: "",
    reasons: "",
    isActive: "",
  });
  const [pageLoader, setPageLoader] = useState(true);
  const [uid, setUid] = useState<string>("");

  const toastMessageRef = useRef(null);
  const [toastMessage, setToastMessage] = useState("");
  const [errorMessage, setErrorMessage] = useState("");
  const [toastMessageVariant, setToastMessageVariant] = useState(TOAST_STYLE_TYPE.PRIMARY);
  const [triggerToast, setTriggerToast] = useState(false);
  const [showDialogAPIError, setShowDialogAPIError] = useState(false);
  const [position, setPosition] = useState<ToastPositionType | undefined>("bottom-right");
  const [navigateBack, setNavigateBack] = useState(false);
  // THIS IS TO SET THE ERROR MESSAGE
  const [, forceUpdate] = useState<undefined | boolean>(undefined);

  const simpleValidator = useRef(
    new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    })
  );
  const editorRef = useRef<IRuleEditorRef>(null);

  /*
    -------------------------------------------------------
      Hooks: useEffect
    -------------------------------------------------------
  */
  useEffect(() => {
    const parsed: {
      mode?: string;
      uid?: string;
    } = queryString.parse(props.location.search);
    if (parsed?.mode && parsed?.mode !== undefined) {
      setPageMode(parsed.mode);
    }
    if (parsed?.uid && parsed?.uid !== undefined) {
      setUid(parsed?.uid);
      BusinessRuleService.findById(parsed.uid)
        .then((res: any) => {
          if (validateGetAPIResponse(res)) {
            console.log(res);
            if (Object.keys(res.data).length > 0 || res.data.id) {
              setPageObj({
                ruleName: res?.data?.name,
                rule: res?.data?.rule,
                description: res?.data?.description,
                reasons: res?.data?.reason,
                isActive: res?.data?.isActive ? ACTIVE_TYPE.ACTIVE : ACTIVE_TYPE.INACTIVE,
              });
              setInitialObj({ ...res?.data });
              setPageLoader(false);
            }
          }
        })
        .catch((err) => {
          // IF API fails to load, show the error message
          setPageLoader(false);
        });
    } else {
      setPageLoader(false);
    }
  }, []);
  useEffect(() => {
    if (errorMessage) {
      setShowDialogAPIError(true);
    }
  }, [errorMessage]);
  useEffect(() => {
    if (toastMessageRef?.current && toastMessage !== "") {
      // @ts-ignore: will be fixed in future
      toastMessageRef?.current?.showFunction();
    }
  }, [toastMessage, toastMessageVariant, triggerToast]);

  /*
    -------------------------------------------------------
      Handlers: Event handler for onChange, onBlur etc
    -------------------------------------------------------
  */

  const handleInputChange = (event: any) => {
    const { name, value } = event.target;
    setPageObj((prevPageObj) => ({
      ...prevPageObj,
      [name]: value,
    }));
  };

  /* 
    -------------------------------------------------------
      ButtonClick Functions:
    -------------------------------------------------------
  */

  const onSaveClick = async () => {
    setErrorMessage("");
    let isValid = false;
    isValid = await validateForm();
    if (!isValid) {
      setPosition("top-right");
      setToastMessageVariant(TOAST_STYLE_TYPE.QUATERNARY);
      setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.ERROR);
      setTriggerToast(!triggerToast);
      return;
    }
    if (pageMode === PAGE_MODE.CREATE) {
      const enrichedObj = prepareToSave();
      return BusinessRuleService.save(enrichedObj)
        .then((res: any) => {
          if (validatePostAPIResponse(res)) {
            setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
            setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SAVED);
            setTriggerToast(!triggerToast);
            setNavigateBack(true);
          }
        })
        .catch((err) => {
          setErrorMessage(err?.response?.data?.message ?? "");
        });
    } else if (pageMode === PAGE_MODE.EDIT) {
      const enrichedObj = prepareToUpdate();
      return BusinessRuleService.update(uid, enrichedObj)
        .then((res: any) => {
          if (validateGetAPIResponse(res)) {
            setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
            setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SAVED);
            setTriggerToast(!triggerToast);
            setNavigateBack(true);
          }
        })
        .catch((err) => {
          setErrorMessage(err?.response?.data?.message ?? "");
        });
    }
  };

  const onCancelClick = () => {
    props.navigate(linkProductConfigurationBusinessRule);
  };

  const onApproveClick = async () => {
    console.log("this is onSaveClick");
    setErrorMessage("");
    let isValid = false;
    isValid = await validateForm();
    if (!isValid) {
      return;
    }
    const enrichedObj = prepareToApprove();
    return BusinessRuleService.approve(enrichedObj)
      .then((res) => {
        setToastMessageVariant(TOAST_STYLE_TYPE.PRIMARY);
        setToastMessage(STATIC_MASTER_TYPE_TOAST_TEXT.SENT_FOR_APPROVAL);
        setTriggerToast(!triggerToast);
        setNavigateBack(true);
      })
      .catch((err) => {
        setErrorMessage(parseStandardAPIErrorMessage(err));
      });
  };

  const onBackClick = () => {
    props.navigate(linkProductConfigurationBusinessRule);
  };

  const onEditClick = () => {
    setPageMode(PAGE_MODE.EDIT);
  };

  /* 
  -----------------------------------------------------------------------------------------------------------------------
  Validators:
  -----------------------------------------------------------------------------------------------------------------------
*/

  const validateForm = async () => {
    let isValid = false;
    isValid = simpleValidator?.current?.allValid() ?? false;
    if (isValid) {
      return isValid;
    } else {
      forceUpdate((prev) => !prev);
      simpleValidator?.current?.showMessages();
      forceUpdate((prev) => !prev);
      return false;
    }
  };

  /* 
    -------------------------------------------------------
      Helper Functions:
    -------------------------------------------------------
  */
  const getPageTitle = () => {
    switch (pageMode) {
      case PAGE_MODE.CREATE:
        return "heading.productConfiguration.businessRule.create";
      case PAGE_MODE.VIEW:
        return "heading.productConfiguration.businessRule.view";
      case PAGE_MODE.EDIT:
        return "heading.productConfiguration.businessRule.update";
      default:
        return "heading.productConfiguration.businessRule.create";
    }
  };
  const prepareToSave = () => {
    const enrichForSave = {
      name: pageObj.ruleName,
      rule: editorRef?.current?.getValue() ?? "",
      description: pageObj.description,
      reason: pageObj.reasons,
      isActive: pageObj.isActive === "ACTIVE" ? true : false,
    };
    return enrichForSave;
  };
  const prepareToUpdate = () => {
    // here i will send the data on the update
    const enrichedObj = {
      ...initialObj,
      name: pageObj.ruleName,
      rule: editorRef?.current?.getValue() ?? "",
      description: pageObj.description,
      reason: pageObj.reasons,
      isActive: pageObj.isActive === "ACTIVE" ? true : false,
    };

    return enrichedObj;
  };

  const prepareToApprove = () => {
    let enrichForSave;
    if (pageMode === PAGE_MODE.CREATE) {
      const finalJsonObject = {
        name: pageObj.ruleName,
        rule: editorRef?.current?.getValue() ?? "",
        description: pageObj.description,
        reason: pageObj.reasons,
        isActive: pageObj.isActive,
      };
      enrichForSave = {
        name: pageObj.ruleName,
        rule: editorRef?.current?.getValue() ?? "",
        description: pageObj.description,
        reason: pageObj.reasons,
        isActive: pageObj.isActive,
        operationType: OPERATION_TYPE.CREATE,
        entity: ENTITY.BUSINESS_RULE,
        initialStateJson: "",
        finalStateJson: stringifyObject(finalJsonObject),
        reqPayload: stringifyObject(finalJsonObject),
        reasonForChange: pageObj.reasons,
      };
    } else {
      const initialJsonObject = initialObj;
      const finalJsonObject = prepareToUpdate();
      enrichForSave = {
        operationType: OPERATION_TYPE.UPDATE,
        entity: ENTITY.BUSINESS_RULE,
        initialStateJson: stringifyObject(initialJsonObject),
        finalStateJson: stringifyObject(finalJsonObject), //this is what
        reqPayload: stringifyObject(finalJsonObject), //this is what
        reasonForChange: pageObj.reasons, //this is what
        name: pageObj.ruleName,
        rule: editorRef?.current?.getValue() ?? "",
        description: pageObj.description,
        reason: pageObj.reasons,
        isActive: pageObj.isActive,
      };
    }
    return enrichForSave;
  };

  /* 
    -------------------------------------------------------
      Sectional Render Functions: For Sub components / section wise renders
    -------------------------------------------------------
  */
  const { ruleName, rule, description, reasons } = pageObj;
  if (pageLoader)
    return (
      <div>
        <Loader />
      </div>
    );
  return (
    <AclGuard>
      <div className="flex flex-col gap-3 ">
        <div className="flex w-full justify-between items-center">
          <PageTitle title={getPageTitle()} />

          <div className="flex ">
            {pageMode === PAGE_MODE.CREATE || pageMode === PAGE_MODE.EDIT ? (
              <div className="flex gap-10">
                <div className="flex gap-5">
                  <ButtonBox buttonType={BUTTON_TYPE.CANCEL_OUTLINE} onClick={onCancelClick} />
                  <ButtonBox
                    buttonType={BUTTON_TYPE.SAVE_OUTLINE}
                    onClickWithLoader={onSaveClick}
                  />
                </div>
                <div>
                  <ButtonBox
                    className="approve-primary"
                    buttonType={BUTTON_TYPE.SEND_FOR_APPROVAL}
                    onClickWithLoader={onApproveClick}
                  />
                </div>
              </div>
            ) : (
              <div className="flex gap-5">
                <ButtonBox buttonType={BUTTON_TYPE.BACK_OUTLINE} onClick={onBackClick} />
                <ButtonBox
                  buttonType={BUTTON_TYPE.EDIT_OUTLINE}
                  className="blue-primary"
                  onClick={onEditClick}
                />
              </div>
            )}
          </div>
        </div>
        <AclGuard override={{ read: PAGE_MODE.VIEW === pageMode }}>
          <div className="grid grid-cols-4 gap-3">
            <div className="col-span-1">
              <InputBox
                label="input.productConfiguration.businessRule.name"
                name="ruleName"
                variant={INPUT_STYLE_TYPE.SECONDARY}
                onChange={handleInputChange}
                value={ruleName}
                disabled={pageMode === PAGE_MODE.VIEW}
                onBlur={validateForm}
                validation={simpleValidator?.current?.message(
                  "ruleName",
                  pageObj.ruleName,
                  "mandatory"
                )}
              />
            </div>

            <div className="col-span-4">
              <Label label="input.productConfiguration.businessRule.rule" className="col-span-4" />
              <MonacoEditor
                ref={editorRef}
                value={rule}
                disabled={pageMode === PAGE_MODE.VIEW || pageMode === PAGE_MODE.REVIEW}
              />
            </div>

            <div className="col-span-2">
              <InputTextArea
                label="input.productConfiguration.businessRule.description"
                name="description"
                variant={INPUT_STYLE_TYPE.SECONDARY}
                maxCount={200}
                onChange={handleInputChange}
                value={description}
                disabled={pageMode === PAGE_MODE.VIEW}
              />
            </div>
            <div className="col-span-2">
              <InputTextArea
                label="input.productConfiguration.businessRule.reasons"
                name="reasons"
                variant={INPUT_STYLE_TYPE.SECONDARY}
                maxCount={200}
                onChange={handleInputChange}
                value={reasons}
                disabled={pageMode === PAGE_MODE.VIEW}
                onBlur={validateForm}
                validation={simpleValidator?.current?.message(
                  "reasons",
                  pageObj.reasons,
                  "mandatory"
                )}
              />
            </div>
          </div>
        </AclGuard>
        <div>
          <ToastCustom
            position={position}
            ref={toastMessageRef}
            toastText={toastMessage}
            variant={toastMessageVariant}
            onRemove={() => {
              if (navigateBack) {
                props.navigate(linkProductConfigurationBusinessRule);
              }
            }}
          />

          <ErrorDialog
            onHide={() => setShowDialogAPIError(false)}
            visible={showDialogAPIError}
            errorMessage={errorMessage}
          />
        </div>
      </div>
    </AclGuard>
  );
};

export default withRouter(PageBusinessRulesCM);
